button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
  }

  .btn {
    margin: 15px 0;
    padding: 10px 10px;
    text-transform: uppercase;
    letter-spacing: 0.2em;
    line-height: 100%;
    border-radius: 3px;
    opacity: 0.9;
    -webkit-transition: opacity 0.3s ease;
    -o-transition: opacity 0.3s ease;
    transition: opacity 0.3s ease;
  }

  .btn:hover {
    opacity: 1;
  }

  .btn.primary {
    background: #ddd;
    color: #222;
  }
  
  .btn:hover {
    background-color: #748DFF;
  }


